.degenPageContent{
    padding: 30px;
    width: 100%;
    height: 100%;
    overflow-y: auto;
}
.degenPageContent  h3 {
    font-family: 'PoppinsSemiBold';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 33px;
    letter-spacing: 0.25em;
    color: #FFFFFF;
    transform: rotate(-0.82deg);
  }
  .degenPageContent p {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.07em;
    color: #999999;
    transform: rotate(-0.82deg);
    margin-bottom: 30px;
  }
  .degens {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-left: -16px;
    margin-right: -16px;
  }

  .avatarDegen {
    min-width: 225px;
    max-width: 225px;
    min-height: 225px;
    max-height: 225px;
    border-radius: 12px;
    cursor: pointer;
    margin:25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #323343;
    
  }
  .avatarDegen:hover{
    background-color: #323343b4;
  }
  .avatarDegenPlus{
    border-radius: 27px;
  }
.info h4 {
    font-family: 'PoppinsMedium';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 100%;
    letter-spacing: 0.075em;
    color: #FFFFFF;
}
.info span {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 275;
    font-size: 9px;
    line-height: 9px;
    letter-spacing: 0.075em;
    color: #FFFFFF;
    }
    
.degenBaseImageContainer{
  min-width: 225px;
  max-width: 225px;
  max-height: 225px;
  min-height: 225px;
  border-radius: 15px;
  position: relative;
  margin:25px;
}
.degenBaseImageLayer{
  cursor: pointer;
  min-width: 225px;
  max-width: 225px;
  max-height: 225px;
  min-height: 225px;
  border-radius: 15px;
  position: absolute;
}